@import "https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap";
body {
  background-color: #292b35;
  margin: 0;
  padding: 0;
  font-family: Lato, sans-serif;
}

html {
  scroll-behavior: smooth;
}

nav {
  background-color: #37485f;
}

nav .nav-container {
  justify-content: space-between;
  display: flex;
}

nav .tech-container .fa-brands {
  margin: 0 .3rem;
  font-size: 1.4rem;
}

nav .fa-meteor {
  color: #fff;
  margin: 0 10px 0 0;
  font-size: 1.4rem;
}

nav h3 {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 0 30px 0 0;
}

nav .menu-container, nav .tech-container {
  align-items: center;
  padding: 20px;
  display: flex;
}

nav .nav-link {
  color: #d8dadd;
  border-radius: 6px;
  padding: 8px 12px;
  text-decoration: none;
}

nav .nav-link:hover {
  background-color: #394353;
}

nav .hamburger {
  display: none;
}

nav .hamburger-list-container .nav-link {
  display: block;
}

.hidden {
  display: none !important;
}

.hamburger-list-container {
  display: none;
}

@media (width <= 1000px) {
  nav h3 {
    display: none;
  }
}

@media (width <= 600px) {
  nav .hamburger {
    color: #fff;
    cursor: pointer;
    background-color: #0000;
    border: none;
    font-size: 20px;
    display: block;
  }

  .menu-container .nav-links {
    display: none;
  }

  .hamburger-list-container {
    display: block;
  }
}

section {
  color: #d8d8d8;
}

section h3 {
  margin: 0;
  font-size: 30px;
}

section a {
  color: #d8d8d8;
  text-decoration: none;
}

h1 {
  margin: 0;
  font-size: 70px;
}

p {
  font-size: 18px;
}

footer {
  background-color: #039090;
  justify-content: flex-end;
  padding: 50px;
  display: flex;
}

footer a {
  color: #fff;
  text-decoration: none;
}
